import { Component, OnInit, HostBinding } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslatorService } from 'src/app/services/translator.service';
import { SettingsService } from '../../services/settings.service';

@Component({
  selector: 'pass-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit {

  constructor(
    route: ActivatedRoute,
    translator: TranslatorService,
    private settings: SettingsService
  ) { 
    const lang = route.snapshot.params.lang;
    if (lang) {
      translator.useLanguage(lang);
    } else {
      translator.useBrowserLanguage();
    }
    document.documentElement.lang = translator.getCurrentLanguage(); 
    this.settings.setWindowsSize(window.innerWidth);
  }

  ngOnInit(): void { 
    const translatedTitle = document.body.getElementsByTagName('title')[0];
    document.head.appendChild(translatedTitle);

    // if (this.isMinWidth()) {
    //   const viewDelayTimer = setTimeout(() => {
    //     clearTimeout(viewDelayTimer);
    //     document.getElementsByClassName('slowly')[0].remove();
    //   }, 2500);
    // }
  }

  isMinWidth(): boolean {
    return window.innerWidth < 501;
  }

  isMobile(): boolean {
    return this.settings.isMobileSize();
  }

  isView(type: string): boolean {
    return this.settings.getSection(type).view;
  }

  @HostBinding('bg-theme .burger.open') 
  get isOpenMenu() { 
    return this.settings.isOpenMenu();
  };

  @HostBinding('a.lang') 
  get isOpenLang() { 
    return this.settings.isOpenLang();
  };
}
