import { Injectable } from '@angular/core';
import { v4 as uuidv4 } from 'uuid';
import { Observable, Subscriber } from 'rxjs';
import { StorageService } from './storage.service';
import { TranslatorService } from './translator.service';

@Injectable({
  providedIn: 'root'
})
export class SingleSignOnService {

  private iss = 'https://accounts.pubg.com/oidc';
  private loginApi = '/auth';
  private logoutApi = '/session/end'
  private responseType = 'id_token';
  private redirectUri;
  private dc = window['decodeURIComponent'];
  private ec = window['encodeURIComponent'];

  // change above
  private clientId = 'ee7cd672-dba7-40ec-a32e-d6a52f073541';
  private scope = 'openid'; // openid+email+ ...

  public isLogin$: Observable<boolean>;
  public loginSubscribers$: Subscriber<boolean>[] = [];

  constructor(
    private storage: StorageService,
    private translator: TranslatorService
  ) {
    this.redirectUri = window.location.origin;

    this.login = this.login.bind(this);
    this.logout = this.logout.bind(this);
    this.handleOidcCallback = this.handleOidcCallback.bind(this);

    window['login'] = this.login;

    if (this.isLogoutCallback() && window.opener.window !== window) {
      window.close();
    }

    this.handleOidcCallback();

    let loginStatus = false;
    if (this.checkLocalToken()) {
      loginStatus = true;
    }

    this.isLogin$ = new Observable<boolean>(subscrber => {
      this.loginSubscribers$.push(subscrber)
      subscrber.next(loginStatus);
    });
  }

  private buildLoginUrl(): string {
    const nonce = window.btoa(uuidv4());
    this.storage.set('nonce', nonce);
    const state = window.btoa(uuidv4());
    this.storage.set('state', state);

    return `${this.iss}${this.loginApi}?response_type=${this.ec(this.responseType)}&nonce=${this.ec(nonce)}`
      + `&client_id=${this.clientId}&redirect_uri=${this.ec(this.redirectUri)}`
      + `&scope=${this.ec(this.scope)}`
      + `&state=${this.ec(state)}&ui_locales=${this.translator.getCurrentLanguage()}`;
  }

  public login() {
    location.href = this.buildLoginUrl();
  }

  public logout() {
    const aud = this.storage.get('pubg.token.key');
    const token = this.storage.get(`pubg.access.token.key${aud}`);
    this.clearLocalToken();
    for (const sub of this.loginSubscribers$) {
      sub.next(false);
    }


    var positionX = (window.innerWidth - 410) / 2 + window.screenLeft;
    var positionY = (window.innerHeight - 430) / 2 + window.screenTop;

    window.open(`${this.iss}${this.logoutApi}?id_token_hint=${token}` +
      `&post_logout_redirect_uri=${this.redirectUri}&state=${btoa('logout')}` +
      `&ui_locales=${this.translator.getCurrentLanguage()}`,
      'PASS.PUBG.COM',
      `width=410, height=430, toolbar=no, resizable=0, left=${positionX}, top=${positionY}`);
  }

  public handleOidcCallback() {
    const hash = window['location']['hash'];
    this.clearHash();

    if (hash) {
      const params = new URLSearchParams(hash.substr(1));

      const state = this.dc(params.get('state'));

      const localState = this.storage.get('state');
      this.storage.delete('state');

      if (state !== localState) {
        console.error('state different');
      }

      try {
        const token = params.get('id_token');
        const tokenBody = token.split('.')[1];
        const tokenObj = JSON.parse(atob(tokenBody));

        if (tokenObj.nonce !== this.storage.get('nonce')) {
          this.storage.delete('nonce');
          return;
        }

        this.storage.delete('nonce');

        if (!this.verifyToken(tokenObj)) {
          return;
        }

        this.storage.set('pubg.token.key', tokenObj.aud);
        this.storage.set('pubg.nonce.idtoken', tokenObj.nonce);
        this.storage.set(`pubg.access.token.key${tokenObj.aud}`, token);
        this.storage.set(`pubg.expiration.key${tokenObj.aud}`, tokenObj.exp);
      }
      catch (e) {
        console.error(e);
      }

    }
  }

  private clearHash() {
    window.location.hash = '';
  }

  private verifyToken(tokenObj: any): boolean {

    if (!tokenObj.nonce || !tokenObj.aud || !tokenObj.iss ||
      !tokenObj.sub || !tokenObj.exp || !tokenObj.iat) {
      console.error('token does not have required field');
      return false;
    }

    if (tokenObj.aud !== this.clientId) {
      console.error('wrong token aud');
      return false;
    }


    if (tokenObj.exp - Date.now() / 1000 <= 0) {
      console.error('expired token');
      return false;
    }

    // doesn't work locally
    if (tokenObj.iss !== this.iss) {
      console.error('wrong token iss')
      return false;
    }

    return true;
  }

  private checkLocalToken() {
    try {
      const aud = this.storage.get('pubg.token.key');
      if (!aud) {
        return false;
      }

      const exp = this.storage.get(`pubg.expiration.key${aud}`);
      const token = this.storage.get(`pubg.access.token.key${aud}`);

      if (Number(exp + '000') - Date.now() <= 0) {
        this.clearLocalToken()
        return false;
      }

      if (!this.verifyToken(JSON.parse(atob(token.split('.')[1])))) {
        this.clearLocalToken()
        return false;
      }

      return true;
    } catch (e) {
      console.error(e);
      this.clearLocalToken();
    }


    return false;
  }

  private clearLocalToken() {
    const aud = this.storage.get('pubg.token.key');

    if (aud) {
      this.storage.delete(`pubg.access.token.key${aud}`);
      this.storage.delete(`pubg.expiration.key${aud}`);
      this.storage.delete('pubg.token.key');
      this.storage.delete('pubg.nonce.idtoken');
    }
  }

  private isLogoutCallback() {
    if (window.location.search) {
      const params = new URLSearchParams(window.location.search.substr(1));
      if (params.get('state') === btoa('logout')) {
        return true;
      }
    }
    return false;
  }
}
