import { Component, HostListener, OnInit } from '@angular/core';

@Component({
  selector: 'pass-mobile-item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.scss']
})
export class PassMobileItemComponent implements OnInit {

  activeItem: number;
  startX: number;

  constructor() { 
    this.activeItem = 0;
    this.startX = 0;
  }

  ngOnInit(): void { 
    this.setActiveBar(0);
  }

  @HostListener('touchstart', ['$event']) 
  onTouchStart(event) {
    this.startX = event.touches[0].clientX;
  }

  @HostListener('touchend', ['$event']) 
  onTouchEnd(event) {
    const dir = event.changedTouches[0].clientX - this.startX;

    if (dir < 20 && dir > -20) return;  //  

    if (dir < 0) {
      this.activeItem += 1;
    } else {
      this.activeItem -= 1;
    }

    if (this.activeItem > 2) {
      this.activeItem = 0;
    } else if (this.activeItem < 0) {
      this.activeItem = 0;
    }

    // const children = document.getElementsByClassName('point-wrapper');
    // for (let i = 0; i < children.length; i++) {
    //   const nested = children[i].children;
    //   for (let j = 0; j < nested.length; j++) {
    //     nested[j].
    //   }
    // }

    this.setActiveBar(this.activeItem);
  }

  private setActiveBar(index: number) {
    const activeCharacter = document.getElementsByClassName('active-character')[0];
    const activeBar = document.getElementsByClassName('active-bar')[0];
    const children = activeBar.children;

    for (let i = 0; i < children.length; i++) {
      const className = (i === index) ? 'active' : '';
      children[i].className = className;
      activeCharacter.children[i].className = className;
    }
  }
}
