import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { GtagModule } from 'angular-gtag';

import { AppComponent } from './app.component';
import { LayoutComponent } from './components/layout/layout.component';
import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';
import { PassMainComponent } from './components/sections/main/main.component';
import { PassSeasonComponent } from './components/sections/season/season.component';
import { PassSeasonDescriptionComponent } from './components/sections/season-description/season-description.component';
import { PassVideoComponent } from './components/sections/video/video.component';
import { PassItemComponent } from './components/sections/item/item.component';
import { PassCharacterComponent } from './components/sections/character/character.component';
import { PassMapComponent } from './components/sections/map/map.component';
import { PassBannerComponent } from './components/sections/banner/banner.component';
import { MobileHeaderComponent } from './components/mobile-header/mobile-header.component';
import { MobileFooterComponent } from './components/mobile-footer/mobile-footer.component';
import { PassMobileMainComponent } from './components/mobile-sections/main/main.component';
import { PassMobileSeasonComponent } from './components/mobile-sections/season/season.component';
import { PassMobileSeasonDescriptionComponent } from './components/mobile-sections/season-description/season-description.component';
import { PassMobileVideoComponent } from './components/mobile-sections/video/video.component';
import { PassMobileItemComponent } from './components/mobile-sections/item/item.component';
import { PassMobileCharacterComponent } from './components/mobile-sections/character/character.component';
import { PassMobileMapComponent } from './components/mobile-sections/map/map.component';
import { PassMobileBannerComponent } from './components/mobile-sections/banner/banner.component';

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    AppComponent,
    LayoutComponent,
    HeaderComponent,
    FooterComponent,
    PassMainComponent,
    PassSeasonComponent,
    PassSeasonDescriptionComponent,
    PassVideoComponent,
    PassItemComponent,
    PassCharacterComponent,
    PassMapComponent,
    PassBannerComponent,
    MobileHeaderComponent,
    MobileFooterComponent,
    PassMobileMainComponent,
    PassMobileSeasonComponent,
    PassMobileSeasonDescriptionComponent,
    PassMobileVideoComponent,
    PassMobileItemComponent,
    PassMobileCharacterComponent,
    PassMobileMapComponent,
    PassMobileBannerComponent
  ],
  imports: [
    HttpClientModule,
    BrowserModule,
    RouterModule.forRoot([
      { path: '', component: LayoutComponent },
      { path: ':lang', component: LayoutComponent }
    ]),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    GtagModule.forRoot({ trackingId: 'DC-9628721', trackPageviews: true }),
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
