import { Component, OnInit } from '@angular/core';
import { TranslatorService } from '../../../services/translator.service';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

const VideoInfo = {
  'zh-hans': {
    type: 'bilibili',
    link: '//player.bilibili.com/player.html?aid=668898795&bvid=BV1ea4y1a7Jp&cid=215303250&page=1'
  },
  'defalut': {
    type: 'youtube',
    link: 'http://www.youtube.com/embed/7vNs8iFgRM0?enablejsapi=1&origin=http://example.com'
  }
}

@Component({
  selector: 'pass-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.scss']
})
export class PassVideoComponent implements OnInit {

  videoType: string;
  videoLink: SafeResourceUrl;

  constructor(
    private sanitize: DomSanitizer,
    private translator: TranslatorService
  ) { }

  ngOnInit(): void { 
    const currentLang = this.translator.getCurrentLanguage();
    const video = VideoInfo[currentLang];
    if (video) {
      this.videoType = video.type;
      this.videoLink = this.sanitize.bypassSecurityTrustResourceUrl(video.link);
    } else {
      const defulat = VideoInfo['defalut']
      this.videoType = defulat.type;
      this.videoLink = this.sanitize.bypassSecurityTrustResourceUrl(defulat.link);
    }
  }

}
