import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root'
})
export class TranslatorService {

  private defaultLanguage: string = 'en';

  private availablelangs = [
    { code: 'en', text: 'English (Global)' },
    { code: 'en-us', text: 'English (NA)' },
    { code: 'ko', text: 'Korean' },
    { code: 'fr', text: 'French' },
    { code: 'it', text: 'Italian' },
    { code: 'de', text: 'German' },
    { code: 'es', text: 'Spanish [ES]' },
    { code: 'es-al', text: 'Spanish [AL]' },
    { code: 'id', text: 'Indonesia' },
    { code: 'ja', text: 'Japanese' },
    { code: 'pl', text: 'Polish' },
    { code: 'pt-br', text: 'Portuguese [BR]' },
    { code: 'ru', text: 'Russian' },
    { code: 'th', text: 'Thai' },
    { code: 'tr', text: 'Turkish' },
    { code: 'vi', text: 'Vietnamese' },
    { code: 'zh-hans', text: 'Simplified Chinese' },
    { code: 'zh-hant', text: 'Traditional Chinese' },
    { code: 'ar', text: 'Arabic' },
  ];

  constructor(public translate: TranslateService) { 
    if (!translate.getDefaultLang())
      translate.setDefaultLang(this.defaultLanguage);

    this.useLanguage();
  }

  useLanguage(lang: string = null) {
    this.translate.use(lang || this.translate.getDefaultLang());
    this.translate.currentLang = lang;
  }

  useBrowserLanguage() {
    this.translate.use(this.translate.getBrowserLang());
  }

  getAvailableLanguages() {
    return this.availablelangs;
  }

  getCurrentLanguage() {
    return this.translate.currentLang;
  }
}
