import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'pass-mobile-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class PassMobileMainComponent implements OnInit {

  constructor() { }

  ngOnInit(): void { }

  applyThemeColor(title: string) {
    const titles = title.split('-');
    const h3Children = document.getElementsByTagName('h3')[0].children;
    
    let children = [];
    titles.forEach((text, index) => {
      const span = document.createElement('span');
      if (index > 0) {
        if(titles[index-1].length === 0 && titles[index+1]?.length === 0) {
          span.textContent = text;
          span.className = 'txt-theme';
          children.push(span);
        } else if (titles[index].length > 0) {
          span.textContent = text;
          children.push(span);
        }
      } else if (titles[index].length > 0) {
        span.textContent = text;
          children.push(span);
      }
    });

    children.forEach((element, index) => {
      h3Children[index].textContent = element.textContent;
      h3Children[index].className = element.className;
    });
  }

}
