import { Injectable } from '@angular/core';
import { TranslatorService } from './translator.service';
import { 
  MobileSize, 
  Section, 
  Language, 
  MenuType, 
  MenuBuynow, 
  MenuFooter 
} from '../../constants';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {

  private windowSize: number;
  private section: any;
  private languages: Array<any>;
  private mainMenu: Array<any>;
  private buynowMenu: Array<any>;
  private footerMenu: Array<any>;
  private openMenu: boolean;
  private openLang: boolean;

  constructor(private translator: TranslatorService) { 
    this.section = Section;
    this.languages = Language;
    this.mainMenu = [];
    this.buynowMenu = [];
    this.footerMenu = [];
    this.openMenu = false;
    this.openLang = false;
  }

  setWindowsSize(size: number) {
    this.windowSize = size;
  }

  getWindowsSize(): number {
    return this.windowSize
  }

  isMobileSize(): boolean {
    return this.windowSize < MobileSize+1;
  }

  getSection(type: string) {

    const keys = type.split('-');

    keys.forEach((key, index) => {
      keys[index] = key.charAt(0).toUpperCase() + key.slice(1);
    });

    const key = keys.join().replace(',', '');

    return this.section[key];
  }

  getLanguageList() {
    return this.languages;
  }

  private createMenu(items: Array<{
    translate: string,
    link?: string,
    external?: boolean,
    target?: string,
    icon?: string,
    submenu?: Array<any>,
    active?: boolean,
    order?: any
  }>, order: Array<number>): Array<any> {

    const lang = this.translator.getCurrentLanguage();
    let sortedMenu: Array<any> = [];

    order.forEach((index) => {

      const item = items[index];

      if (item.submenu && item.order) {
        item.submenu = this.createMenu(item.submenu, item.order[lang]);
      }
      if (item.link) {
        item.link = item.link.replace(':lang', lang);
      }

      sortedMenu.push(item);
    });

    return sortedMenu;
  }

  getMenu(type: MenuType): Array<any> {

    let menu: Array<any>;

    const lang = this.translator.getCurrentLanguage();

    switch (type) {
      case MenuType.BuyNow:
        if (this.buynowMenu.length === 0) {
          this.buynowMenu = this.createMenu(MenuBuynow.items, MenuBuynow.order[lang]);
        }

        menu = this.buynowMenu;
        break;
      case MenuType.Footer:
        if (this.footerMenu.length === 0) {
          this.footerMenu = this.createMenu(MenuFooter.items, MenuFooter.order[lang]);
        }

        menu = this.footerMenu;
        break;
    }

    return menu;
  }

  setOpenMenu(status: boolean) {
    this.openMenu = status;
  }
  
  isOpenMenu(): boolean {
    return this.openMenu;
  }

  setOpenLang(status: boolean) {
    this.openLang = status;
  }

  isOpenLang(): boolean {
    return this.openLang;
  }
}
