import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'pass-character',
  templateUrl: './character.component.html',
  styleUrls: ['./character.component.scss']
})
export class PassCharacterComponent implements OnInit {

  lastEmotActive: number;

  constructor() { 
    this.lastEmotActive = 1;
  }

  ngOnInit(): void { }

  activeEmot(index) {
    this.lastEmotActive = index
  }

}
