import { Component, OnInit, AfterViewInit } from '@angular/core';

@Component({
  selector: 'pass-item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.scss']
})
export class PassItemComponent implements OnInit, AfterViewInit {

  activeItem: number;

  constructor() { }

  ngOnInit(): void { }

  ngAfterViewInit() {
    if(/MSIE \d|Trident.*rv:/.test(navigator.userAgent)) {
      const elems = document.getElementsByClassName('focusie');
      for (let i = 0; i < elems.length; i++) {
        const div1 = document.createElement('div');
        const div2 = document.createElement('div');
        div2.className = 'innerfocus';
        div1.className = 'midfocus';
        div1.appendChild(div2);
        const node = elems.item(i).childNodes[0];
        elems.item(i).insertBefore(div1, node);
        // elems.item(i).appendChild(div1);
        elems.item(i).className += ' active';
      }
    }
  }

  setActiveItem(index: number) {
    this.activeItem = index;
  }

}
